export default {
	name: "tuiRate",
	props: {
		//数量
		quantity: {
			type: Number,
			default: 5
		},
		//当前选中
		current: {
			type: Number,
			default: 0
		},
		//禁用点击
		disabled: {
			type: Boolean,
			default: false
		},
		//大小
		size: {
			type: Number,
			default: 20
		},
		//未选中颜色
		normal: {
			type: String,
			default: "#b2b2b2"
		},
		//选中颜色
		active: {
			type: String,
			default: "#e41f19"
		},
		//未选中是否为空心
		hollow: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			pageX: 0
		};
	},
	methods: {
		handleTap(e) {
			if (this.disabled) {
				return;
			}
			const index = e.currentTarget.dataset.index;
			this.$emit('change', {
				index: Number(index) + 1
			})
		},
		touchMove(e) {
			if (this.disabled) {
				return;
			}
			if (!e.changedTouches[0]) {
				return;
			}
			const movePageX = e.changedTouches[0].pageX;
			const distance = movePageX - this.pageX;

			if (distance <= 0) {
				return;
			}
			let index = Math.ceil(distance / this.size);
			index = index > this.count ? this.count : index;
			this.$emit('change', {
				index: index
			})
		}
	},










	onReady() {
		const className = '.tui-rate-box';
		let query = uni.createSelectorQuery().in(this)
		query.select(className).boundingClientRect((res) => {
			this.pageX = res.left || 0
		}).exec()
	}
}
